var vm = {
  name: "standard",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    list: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  mounted(){
  },
  methods: {
    close() {
      this.$emit("close", false);
    }
  }
};
export default vm;
